// src/app/ranking/page.tsx
"use client";

import { useEffect, useMemo, useState } from "react";
import { motion } from "framer-motion";
import { supabaseBrowser } from "@/lib/supabase/client";

/* =========================
   Tipos da API de ranking
   ========================= */
type RankApiItem = {
  user_id: string;
  name?: string | null;
  email?: string | null;
  points: number;     // soma final
  total?: number;     // alias (compat)
  last_completed_at?: string | null; // opcional (se API enviar)
  trend?: number;     // opcional (variaÃ§Ã£o de posiÃ§Ã£o)
};

type RankApiResponse = {
  ok: boolean;
  leaderboard: RankApiItem[];
  page: number;
  pageSize: number;
  total: number;
  pages: number;
  via?: "view" | "progress" | "points";
  adminClient?: boolean;
};

/* =========================
   Helpers visuais reutilizÃ¡veis
   ========================= */
function Avatar({ name }: { name?: string | null }) {
  const initials = (name || "??")
    .trim()
    .split(/\s+/)
    .slice(0, 2)
    .map((s) => s[0]?.toUpperCase() || "")
    .join("");
  return (
    <div className="h-10 w-10 rounded-full bg-white/10 border border-white/10 flex items-center justify-center text-sm">
      {initials || "?"}
    </div>
  );
}

function Medal({ place }: { place: 1 | 2 | 3 }) {
  const style =
    place === 1
      ? "bg-yellow-400/15 border-yellow-300/30 text-yellow-200"
      : place === 2
      ? "bg-slate-300/15 border-slate-200/30 text-slate-200"
      : "bg-amber-700/15 border-amber-700/30 text-amber-300";
  const label = place === 1 ? "ðŸ¥‡ 1Âº" : place === 2 ? "ðŸ¥ˆ 2Âº" : "ðŸ¥‰ 3Âº";
  return (
    <span className={`text-xs px-2 py-0.5 rounded-md border ${style}`}>{label}</span>
  );
}

function Podium({
  items,
}: {
  items: Array<{ name?: string | null; points: number }>;
}) {
  const [first, second, third] = items;
  if (!first) return null;

  return (
    <div className="grid grid-cols-3 gap-4 mt-6">
      {/* 2Âº */}
      <div className="rounded-2xl p-4 bg-white/[0.03] border border-white/10 flex flex-col items-center justify-center min-h-[160px]">
        {second ? (
          <>
            <Medal place={2} />
            <div className="mt-3">
              <Avatar name={second.name} />
            </div>
            <div className="mt-2 text-sm text-white/80 text-center line-clamp-1">
              {second.name}
            </div>
            <div className="text-xs text-white/60">{second.points} pts</div>
          </>
        ) : (
          <div className="h-20" />
        )}
      </div>

      {/* 1Âº */}
      <div className="rounded-2xl p-5 bg-gradient-to-b from-neutral-900 to-black border border-white/10 flex flex-col items-center justify-center shadow-[0_0_20px_rgba(163,230,53,0.08)] min-h-[180px]">
        <Medal place={1} />
        <div className="mt-3 scale-110">
          <Avatar name={first.name} />
        </div>
        <div className="mt-2 text-white text-center font-medium line-clamp-1">
          {first.name}
        </div>
        <div className="text-sm text-lime-300">{first.points} pts</div>
      </div>

      {/* 3Âº */}
      <div className="rounded-2xl p-4 bg-white/[0.03] border border-white/10 flex flex-col items-center justify-center min-h-[160px]">
        {third ? (
          <>
            <Medal place={3} />
            <div className="mt-3">
              <Avatar name={third.name} />
            </div>
            <div className="mt-2 text-sm text-white/80 text-center line-clamp-1">
              {third.name}
            </div>
            <div className="text-xs text-white/60">{third.points} pts</div>
          </>
        ) : (
          <div className="h-20" />
        )}
      </div>
    </div>
  );
}

function RankRow({
  me,
  i,
  name,
  points,
  lastActive,
  trend = 0,
}: {
  me?: boolean;
  i: number;
  name?: string | null;
  points: number;
  lastActive?: string | null;
  trend?: number;
}) {
  const arrow = trend === 0 ? "â€”" : trend > 0 ? "â†‘" : "â†“";
  return (
    <li
      className={`grid grid-cols-[90px_1fr_160px_140px_180px] items-center gap-3 px-6 py-4
                  hover:bg-white/[0.04] transition-colors
                  ${me ? "bg-lime-400/5 ring-1 ring-lime-300/20" : ""}`}
    >
      <div className="text-sm text-white/60">#{i}</div>
      <div className="flex items-center gap-3">
        <Avatar name={name} />
        <div className="font-medium text-white/90 line-clamp-1">{name || "Sem nome"}</div>
      </div>
      <div className="text-white/80">{points} pts</div>
      <div
        className={`text-sm ${
          trend > 0 ? "text-lime-300" : trend < 0 ? "text-rose-300" : "text-white/50"
        }`}
      >
        {arrow} {trend !== 0 ? Math.abs(trend) : ""}
      </div>
      <div className="text-sm text-white/60">
        {lastActive ? new Date(lastActive).toLocaleDateString("pt-BR") : "â€”"}
      </div>
    </li>
  );
}

function RankSkeletonRow() {
  return (
    <li className="grid grid-cols-[90px_1fr_160px_140px_180px] gap-3 px-6 py-4 animate-pulse">
      <div className="h-3 w-8 bg-white/10 rounded" />
      <div className="flex items-center gap-3">
        <div className="h-10 w-10 rounded-full bg-white/10" />
        <div className="h-3 w-48 bg-white/10 rounded" />
      </div>
      <div className="h-3 w-16 bg-white/10 rounded" />
      <div className="h-3 w-10 bg-white/10 rounded" />
      <div className="h-3 w-24 bg-white/10 rounded" />
    </li>
  );
}

function RankEmpty() {
  return (
    <div className="px-6 py-12 text-center text-white/60">
      Ainda nÃ£o hÃ¡ participantes no ranking.
    </div>
  );
}

/* =========================
   PÃ¡gina
   ========================= */
export default function RankingPage() {
  const supabase = useMemo(() => supabaseBrowser(), []);
  const [currentUserId, setCurrentUserId] = useState<string | null>(null);

  const [items, setItems] = useState<RankApiItem[]>([]);
  const [loading, setLoading] = useState(true);
  const [err, setErr] = useState<string | null>(null);

  const [page, setPage] = useState(1);
  const [pageSize] = useState(25);
  const [pages, setPages] = useState(1);
  const [total, setTotal] = useState(0);
  const [refreshTick, setRefreshTick] = useState(0);

  useEffect(() => {
    // pega o id do usuÃ¡rio logado para destacar na tabela
    supabase.auth.getUser().then((res) => {
      setCurrentUserId(res.data.user?.id ?? null);
    });
  }, [supabase]);

  useEffect(() => {
    const controller = new AbortController();
    async function load() {
      setLoading(true);
      setErr(null);
      try {
        const r = await fetch(
          `/api/ranking?page=${page}&pageSize=${pageSize}`,
          { signal: controller.signal, cache: "no-store" }
        );
        const json = (await r.json()) as RankApiResponse | { error?: string };
        if (!("ok" in json) || (json as RankApiResponse).ok !== true) {
          throw new Error((json as any)?.error || "Falha ao carregar ranking");
        }
        const data = json as RankApiResponse;
        const normalized = (data.leaderboard || []).map((x) => ({
          ...x,
          points: Number.isFinite(x.points) ? x.points : (Number(x.total) || 0),
        }));
        setItems(normalized);
        setPages(data.pages);
        setTotal(data.total);
      } catch (e: any) {
        if (e?.name !== "AbortError") {
          setErr(e?.message || "Erro inesperado");
        }
      } finally {
        setLoading(false);
      }
    }
    load();
    return () => controller.abort();
  }, [page, pageSize, refreshTick]);

  const participants = total; // total de usuÃ¡rios ranqueados
  const top3 = items.slice(0, 3);

  return (
    <div className="max-w-6xl mx-auto px-5 md:px-8 py-8 md:py-10">
      {/* HERO */}
      <motion.div
        initial={{ opacity: 0, y: 8 }}
        animate={{ opacity: 1, y: 0 }}
        className="relative overflow-hidden rounded-3xl p-6 md:p-8
                   bg-gradient-to-b from-neutral-900 to-black
                   shadow-[inset_0_0_0_1px_rgba(255,255,255,0.05)]"
      >
        <div className="flex items-start justify-between gap-4 flex-wrap">
          <div>
            <h1 className="text-2xl md:text-3xl font-semibold text-white/90">
              Ranking Geral
            </h1>
            <p className="text-white/60 mt-1">Veja quem estÃ¡ subindo no topo</p>
            <div className="mt-3 flex flex-wrap items-center gap-2">
              <span className="text-[11px] px-2 py-1 rounded-md bg-white/10 border border-white/10">
                <span className="inline-flex h-2 w-2 rounded-full bg-lime-400 mr-1.5 animate-pulse" />
                {loading ? "Atualizando..." : "Atualizado agora"}
              </span>
              <span className="text-[11px] px-2 py-1 rounded-md bg-white/10 border border-white/10">
                {participants} participantes
              </span>
              <button
                onClick={() => setRefreshTick((t) => t + 1)}
                className="text-[11px] px-2 py-1 rounded-md bg-white/10 border border-white/10 hover:bg-white/15"
              >
                Atualizar
              </button>
            </div>
          </div>

          {/* filtros placeholder (mantÃ©m consistÃªncia visual; plugue depois se quiser) */}
          <div className="flex items-center gap-2">
            <select className="bg-white/10 border border-white/10 text-sm rounded-lg px-2 py-1.5">
              <option>All-time</option>
              <option>Semana</option>
            </select>
            <select className="bg-white/10 border border-white/10 text-sm rounded-lg px-2 py-1.5">
              <option>Global</option>
              <option>Meu curso</option>
            </select>
          </div>
        </div>

        {/* PÃ“DIO */}
        {!loading && top3.length > 0 && (
          <Podium items={top3.map((x) => ({ name: x.name, points: x.points }))} />
        )}
      </motion.div>

      {/* ERRO */}
      {err && (
        <div className="mt-4 p-3 rounded-xl border border-rose-400/30 bg-rose-400/10 text-sm">
          {err}
        </div>
      )}

      {/* LISTA */}
      <div className="mt-6 rounded-2xl overflow-hidden bg-white/[0.03] border border-white/10">
        {/* CabeÃ§alho sticky */}
        <div className="grid grid-cols-[90px_1fr_160px_140px_180px] px-6 py-3 text-xs uppercase tracking-wide text-white/60 border-b border-white/10 sticky top-0 bg-black/50 backdrop-blur">
          <div>PosiÃ§Ã£o</div>
          <div>UsuÃ¡rio</div>
          <div>Pontos</div>
          <div>VariaÃ§Ã£o</div>
          <div>Ãšltima atividade</div>
        </div>

        {loading ? (
          <ul className="divide-y divide-white/10">
            {Array.from({ length: 8 }).map((_, i) => (
              <RankSkeletonRow key={i} />
            ))}
          </ul>
        ) : items.length === 0 ? (
          <RankEmpty />
        ) : (
          <ul className="divide-y divide-white/10">
            {items.map((u, idx) => (
              <RankRow
                key={u.user_id}
                i={(page - 1) * pageSize + idx + 1}
                name={u.name}
                points={u.points}
                lastActive={u.last_completed_at}
                trend={u.trend ?? 0}
                me={u.user_id === currentUserId}
              />
            ))}
          </ul>
        )}

        {/* PaginaÃ§Ã£o */}
        <div className="flex items-center justify-between px-6 py-3 border-t border-white/10">
          <button
            disabled={page === 1 || loading}
            onClick={() => setPage((p) => Math.max(1, p - 1))}
            className="text-sm px-3 py-1.5 rounded-md bg-white/10 border border-white/10 disabled:opacity-40"
          >
            Anterior
          </button>
          <div className="text-xs text-white/60">
            PÃ¡gina {page} de {pages}
          </div>
          <button
            disabled={page >= pages || loading}
            onClick={() => setPage((p) => Math.min(pages, p + 1))}
            className="text-sm px-3 py-1.5 rounded-md bg-white/10 border border-white/10 disabled:opacity-40"
          >
            PrÃ³xima
          </button>
        </div>
      </div>
    </div>
  );
}
